import React from 'react';
import { StyleSheet, Text, TouchableWithoutFeedback, View } from 'react-native';
import Utils, { Logger } from '../../utils/Utils';

export default class TimeDisplay extends React.Component {
  constructor(props) {
    super(props);

    this.state = props.skinState;
    this.uzPlayer = props.player;
  }

  _toggleControls() {
    if (this.state.playbackStatus.durationMillis) {
      const pos = parseInt(this.state.playbackStatus.durationMillis / 1000, 10);
      this.uzPlayer.seek(pos, true);
    }
  }

  render() {
    let view;
    var color = this.state.playbackStatus&&this.state.playbackStatus.error? 'white' : 'red';
    if (this.props.stream === 'live') {
      view = (
        <TouchableWithoutFeedback onPress={this._toggleControls.bind(this)}>
          <View
            {...this.props}
            style={[styles.view]}>
            <View style={[{height: 30, top: 13}]}>
                <Text style={[styles.text, {borderColor: color,borderWidth: 1, color: color, paddingLeft: 2, paddingRight: 2}]}>Live</Text>
            </View>
          </View>
        </TouchableWithoutFeedback>
      )
    } else {
      const positionStr = Utils.secondToHHMMSS(parseInt(this.state.playbackStatus.positionMillis / 1000, 10));
      const durationStr = Utils.secondToHHMMSS(parseInt(this.state.playbackStatus.durationMillis / 1000, 10));
      view = (
        <View
          {...this.props}
          style={styles.view}>
          <Text style={styles.text}>{positionStr + ' / ' + durationStr}</Text>
        </View>
      )
    }

    return view;
  }
}

const styles = StyleSheet.create({
  view: {
    alignSelf: 'flex-end',
    flexDirection: 'row',
    height: 48,
    paddingLeft: 10,
    paddingRight: 10
  },
  text: {
    alignSelf: 'center',
    textAlignVertical: 'center',
    color: 'white',
    fontSize: 16
  },
});
